body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body > #root > div {
  height: 400vh;  
  background-color: #E8EAEE;
  /* margin: 10%; */
}


.App {
  text-align: left;
  height: 150vh;
  /* background-color: #E8EAEE; */
  padding: 10%;
  padding-top: 5%;
  /* margin: 10%; */
}
.name{
  /* background-color: #E8EAEE; */
  padding-bottom: 5%;
  font-family: gill-sans-nova-deco, sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  text-align: center;
  color: #6D7172;
}
.subheader{
  font-family: gill-sans-nova, sans-serif;
  font-style: normal;
  font-weight: 900;
  font-size: 32px;
  color: #EF827D;
}



.background{
    background-color: rgb(250, 236, 196);
    padding-bottom: 2%;
}
#background{
    color: #F8C304;
    padding-left: 1%;
}
.background-list{
    font-family: gill-sans-nova, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 20px;
}

.inspo{
    background-color: #F7CBCB;
    padding-bottom: 2%;
}
#inspo{
    padding-left: 1%;
    color: #EF827D;
}
.inspo-list{
    font-family: gill-sans-nova, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 20px;
}
.design{
    background-color: #97C5D8;
    padding-bottom: 2%;
}
#design{
    padding-left: 1%;
    color: #1E3D69;
}
.design-list{
    list-style: none;
    font-family: gill-sans-nova, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 28px;
    margin-top: 0%;
}
.designItem{
    padding-top: 2%;
    padding-bottom: 2%;
}
.paintingImages{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-left: 0px;
}
#pink2{
    margin-left: 2%;
    margin-right: 2%; 
}
#pink3{
    margin-left: 6%;
}
#pink4{
    margin-left: 2%;
    margin-right: 5%;
}
.drawingImages{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
}
#camera2{
    margin-right: 5%;
}
#camera3{
    margin-left: 2.5%;
}
#collage1{
    margin-left: 5%;
}
.future{
    background-color: #AE7BBA;
}
#future{
    padding-left: 1%;
    color: #340763;
}
.frog{
    background-color: #DCE466;
    padding-bottom: 2%;
}
#frog{
    padding-left: 1%;
    color: #4D513A;
}
#frog-list-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
}
#frogimg{
    grid-row: 1 / 2;
    grid-column: 2 / 3;
    display: -webkit-flex;
    display: flex;
    justify-self: flex-end;
    padding-right: 5%;
    padding-bottom: 5%;
}
.frog-list{
    grid-row: 1 / 2;
    grid-column: 1 / 2;
    font-family: gill-sans-nova, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 20px;
}
.passionProject{
    background-color: #FAC375;
    padding-bottom: 2%;
}
#passionProject{
    padding-left: 1%;
    color: #DE7015;
}
.passionProject-list{
    list-style: none;
    font-family: gill-sans-nova, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 20px;
    padding-right: 5%;
}
#arrayngeimg{
    width: 100%;
    padding-top: 2%;
    padding-bottom: 2%;
}
